import React from "react";
import {Field, reduxForm} from "redux-form";
import {Input} from "../common/FormsControls/FormsControls";
import {MaxLengthCreator, required} from "../../utils/validators/validators";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
import {Redirect} from "react-router-dom";
import style from '../common/FormsControls/FormsControl.module.css'

import s from './Login.module.css';

const maxLength30 = MaxLengthCreator(30);
const maxLength40 = MaxLengthCreator(40);
const LoginForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <div className={s.inputs}>
        <Field className={s.input} placeholder={'Email'} name={'email'} component={Input}
               validate={[required, maxLength30]}/>
      </div>
      <div className={s.inputs}>
        <Field className={s.input} placeholder={'Password'} name={'password'} component={Input} type={'password'}
               validate={[required, maxLength40]}/>
      </div>
      <div className={s.inputs}>
        <Field className={s.input} component={Input} id="rememberMe" name={'rememberMe'} type={'checkbox'}/>
        <label htmlFor="rememberMe">remember me</label>
      </div>
      {props.error && <div className={style.formSummaryError}> {props.error}</div>}
      <div>
        <button className={style.buttons}>Login</button>
      </div>
    </form>
  )
};
const LoginReduxForm = reduxForm({form: 'login'})(LoginForm)

const Login = (props) => {
  const onSubmit = (formData) => {
    props.login(formData.email, formData.password, formData.rememberMe);
  }
  if (props.isAuth) {
    return <Redirect to={'/profile'}/>
  }
  return (<div className={s.loginWrapper}>
      <h1 className={s.title}>Login</h1>
      <LoginReduxForm onSubmit={onSubmit}/>
    </div>
  )
};
const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth
});
export default connect(mapStateToProps, {login})(Login);