import React from "react";
import styles from "./users.module.css";
import userPhotos from "../../assets/images/images.jpg";
import {NavLink} from "react-router-dom";

let User = ({user, followingInProgress, unfollow, follow}) => {
  return <div>
    <div className={styles.usersModule}>
                <span className={styles.usersBlock}>
                    <NavLink to={'/profile/' + user.id}>
                        <img alt="Important" src={user.photos.small != null ? user.photos.small : userPhotos}
                             className={styles.userPhoto}/>
                    </NavLink>
                    <span>
                        <p>{user.name}</p>
                        <p>{user.status}</p>
                    </span>
                    <div className={styles.usersInf}>
                        {user.followed
                          ? <button className={styles.foluflo} disabled={followingInProgress
                            .some(id => id === user.id)}
                                    onClick={() => {
                                      unfollow(user.id);
                                    }}>
                            Unfollow</button>
                          : <button className={styles.foluflo} disabled={followingInProgress
                            .some(id => id === user.id)}
                                    onClick={() => {
                                      follow(user.id);
                                    }}>
                            Follow</button>}
                    </div>
                </span>
    </div>
    <div>
      <hr className={styles.hr}></hr>
    </div>

  </div>

}

export default User;