import React from 'react';
import s from './Header.module.css';
import {NavLink} from "react-router-dom";
import logoPhoto from '../../assets/images/business.png';
import logOut from "../../assets/images/signs.png"
const Header = (props) => {
    return (
        <header className={s.header}>
            <img  className={s.logo} src={logoPhoto} />
            <div className={s.loginBlock}>
                {props.isAuth
                    ? <div className={s.wrapper}>
                        <p className={s.login}>{props.login}</p><button  className={s.buttons} onClick={props.logout} >Log out</button></div>
                    : <NavLink className={s.buttons} to={'/login'}>Login</NavLink>}
            </div>
        </header>
    )
}

export default Header;
